.waves-wrapper {
  width: 100vw;
  position: fixed;
  bottom: 0px;
}

svg {
  width: 100%;
}

.wave {
  animation: wave 3s linear;
  animation-iteration-count: infinite;
  fill: none;
  stroke: #19A8D5;
  stroke-width: 0.2px;
}

#wave2 {
  animation-duration: 5s;
  animation-direction: reverse;
  opacity: .6;
}

#wave3 {
  animation-duration: 7s;
  opacity: .3;
}

@keyframes wave {
  to {
    transform: translateX(-100%);
  }
}
